/* fade-in-vanish-out */
    .fade-in-vanish-out-enter {
        opacity: 0.01;
        -webkit-transform: scale(0.9) translateY(20%);
            -ms-transform: scale(0.9) translateY(20%);
                transform: scale(0.9) translateY(20%);
    }
    .fade-in-vanish-out-enter-active {
        opacity: 1;
        -webkit-transform: scale(1) translateY(0%);
            -ms-transform: scale(1) translateY(0%);
                transform: scale(1) translateY(0%);
        -webkit-transition: all 300ms ease-out;
        -o-transition: all 300ms ease-out;
        transition: all 300ms ease-out;
    }
    .fade-in-vanish-out-exit {
        opacity: 0;
    }
    .fade-in-vanish-out-exit-active {
        opacity: 0;
    }

/* Pop */
    .pop-enter {
        opacity: 0.01;
        -webkit-transform: scale(0.9);
            -ms-transform: scale(0.9);
                transform: scale(0.9);
    }
    .pop-enter-active {
        opacity: 1;
        -webkit-transform: scale(1);
            -ms-transform: scale(1);
                transform: scale(1);
        -webkit-transition: all 200ms ease-out;
        -o-transition: all 200ms ease-out;
        transition: all 200ms ease-out;
    }
    .pop-exit {
        opacity: 1;
        -webkit-transform: scale(1.65);
            -ms-transform: scale(1.65);
                transform: scale(1.65);
    }
    .pop-exit-active {
        opacity: 0.01;
        -webkit-transform: scale(0.5);
            -ms-transform: scale(0.5);
                transform: scale(0.5);
        -webkit-transition: all 100ms;
        -o-transition: all 100ms;
        transition: all 100ms;
    }

/* Pop-Echo */
    .pop-echo-enter {
        opacity: 0.5;
        -webkit-transform: scale(0.9);
            -ms-transform: scale(0.9);
                transform: scale(0.9);
    }
    .pop-echo-enter-active {
        opacity: 0;
        -webkit-transform: scale(2);
            -ms-transform: scale(2);
                transform: scale(2);
        -webkit-transition: all 200ms ease-out;
        -o-transition: all 200ms ease-out;
        transition: all 200ms ease-out;
    }
    .pop-echo-exit {
        opacity: 1;
        -webkit-transform: scale(1.65);
            -ms-transform: scale(1.65);
                transform: scale(1.65);
    }
    .pop-echo-exit-active {
        opacity: 0;
        -webkit-transform: scale(0);
            -ms-transform: scale(0);
                transform: scale(0);
        -webkit-transition: all 0.3s;
        -o-transition: all 0.3s;
        transition: all 0.3s;
    }


